// Componant: Card

.card {
	position: relative;
	background: #ffffff;
	border-radius: 3px;
	padding: 20px;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
	margin-bottom: 30px;
	transition: all 0.3s ease-in-out;
	&.small {
		padding: 0; }

	.overlay {
		background-color: rgba(255,255,255,0.9);
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: row;
		color: #555;
		border-radius: 3px;
		@include position(absolute, 0 0 0 0);
		.l-text {
			font-size: 24px;
			margin-top: 15px; } }

	.card-title {
		margin-top: 0;
		margin-bottom: 20px; }

	.card-title-w-btn {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 20px;
		//border-bottom: 1px solid #ddd
		//padding-bottom: 10px
		p {
			margin: 0; }
		.title {
			margin: 0; }
		.side {
			margin-bottom: 0;
			padding-left: 0; } }

	.card-footer {
		border-top: 1px solid #ddd;
		padding: 20px 0 0 0;
		margin-top: 10px; } }
