// Componant: Page Title

.page-title {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-direction: row;

	background-color: #FFF;
	margin-top: -30px;
	margin-left: -30px;
	margin-right: -30px;
	margin-bottom: 30px;
	padding: 20px 30px;
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);

	h1 {
		margin: 0;
		font-size: 24px;
		font-weight: 400; }
	p {
		margin-bottom: 0;
		font-style: italic; }

	.breadcrumb {
		margin-bottom: 0;
		text-align: right;
		font-weight: 500;
		font-size: 13px;
		text-transform: capitalize;
		&.side {
			margin-top: 10px;
			text-align: left;
			padding: 0; } } }


@media (max-width: 480px) {
	.page-title {
		margin-top: -10px;
		margin-left: -10px;
		margin-right: -10px;
		padding: 20px; } }
